/* Information Section
--------------------------------------------------------------------------------------*/
section:nth-of-type(2) {

    #info{
        display: grid;
        grid-template-columns: repeat(4, 20em);
        grid-template-rows: auto;
        grid-column-gap: 2em;
        justify-content: center;

        padding-top: 2em;
        padding-bottom: 2em;
    }

    figure {
        margin: 0;
        transition-property: margin;
        transition-duration: 0.5s;
        transition-delay: 0s;
        transition-timing-function: ease;

        background-color: $cardsbg;

        h3 {
            text-align: center;
        }

        p {
            padding-left: 1em;
        }
    }

    figure:hover {
        outline: 2px solid #000;
        cursor: pointer;
        margin: -5px 0px 0px -5px;
    }

    figure:nth-of-type(1) {
        grid-column: 1/2;
        grid-row: 1/2;

        width: 100%;
    }

    figure:nth-of-type(2) {
        grid-column: 2/3;
        grid-row: 1/2;
       
        width: 100%;
    }

    figure:nth-of-type(3) {
        grid-column: 3/4;
        grid-row: 1/2;
        
        width: 100%;
    }

    figure:nth-of-type(4) {
        grid-column: 4/5;
        grid-row: 1/2;
        
        width: 100%;
    }

    img {
        display: block;
        width: 100%;
        margin: 0;
    }
}


/*  Media Query Section
--------------------------------------------------------------------------------------*/
@include breakpoints(min-width, 20em) {
    section:nth-of-type(2) {
        #info {
            grid-template-columns: repeat(1, 20em);
            grid-template-rows: repeat(4, auto);
            grid-row-gap: 1em;

            padding-bottom: 1em;
        }

        figure:nth-of-type(1) {
            grid-column: 1/1;
            grid-row: 1/2;
        }
        
        figure:nth-of-type(2) {
            grid-column: 1/1;
            grid-row: 2/3;
        }

        figure:nth-of-type(3) {
            grid-column: 1/1;
            grid-row: 3/4;
        }

        figure:nth-of-type(4) {
            grid-column: 1/1;
            grid-row: 4/5;
        }
    }
}

@include breakpoints(min-width, 40em) {
    section:nth-of-type(2) {
        #info {
            grid-template-columns: repeat(2, minmax(16em, 20em));
            grid-template-rows: repeat(2, auto);
            grid-column-gap: 1em;
            
            padding-bottom: 1em;
        }

        figure:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 1/2;
        }
        
        figure:nth-of-type(2) {
            grid-column: 2/3;
            grid-row: 1/2;
        }

        figure:nth-of-type(3) {
            grid-column: 1/2;
            grid-row: 2/3;
        }

        figure:nth-of-type(4) {
            grid-column: 2/3;
            grid-row: 2/3;
        }
    }
}

@include breakpoints(min-width, 70em) {
    section:nth-of-type(2) {
        #info {
            grid-template-columns: repeat(4, minmax(16em, 20em));
            grid-template-rows: 23em;
            grid-column-gap: 1em;
            grid-row-gap: 0;
            padding-bottom: 1em;
        }

        figure:nth-of-type(1) {
            grid-column: 1/2;
            grid-row: 1/2;
        }
        
        figure:nth-of-type(2) {
            grid-column: 2/3;
            grid-row: 1/2;
        }

        figure:nth-of-type(3) {
            grid-column: 3/4;
            grid-row: 1/2;
        }

        figure:nth-of-type(4) {
            grid-column: 4/5;
            grid-row: 1/2;
        }
    }
}